import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { Input } from 'antd';
import Checkbox from '../../components/uielements/checkbox';
import Button from '../../components/uielements/button';
import Form from '../../components/uielements/form';
import authAction from '../../redux/auth/actions';
import appActions from '../../redux/app/actions';
import IntlMessages from '../../components/utility/intlMessages';
import SignUpStyleWrapper from './signup.style';

const FormItem = Form.Item;
const { login, signup } = authAction;
const { clearMenu } = appActions;

class SignUp extends Component {
  state = {
    redirectToReferrer: false,
    confirmDirty: false,
  };
  componentWillReceiveProps(nextProps) {
    if (
      this.props.isLoggedIn !== nextProps.isLoggedIn &&
      nextProps.isLoggedIn === true
    ) {
      this.setState({ redirectToReferrer: true });
    }
  }
  handleSignUp = (e) => {
    e.preventDefault();
    const { history, signup } = this.props;

    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        const userInfo = {
          username: document.getElementById('inputUserName').value || '',
          firstname: document.getElementById('inputFirstName').value || '',
          lastname: document.getElementById('inputLastName').value || '',
          email: document.getElementById('email').value || '',
          password: document.getElementById('password').value || '',
        };
        console.log(userInfo)
        signup({ history, userInfo });
      } else {
        console.log(err)
      }
    });
  };
  handleConfirmBlur = e => {
    const { value } = e.target;
    this.setState({ confirmDirty: this.state.confirmDirty || !!value });
  };

  compareToFirstPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && value !== form.getFieldValue('password')) {
      callback('Two passwords that you enter is inconsistent!');
    } else {
      callback();
    }
  };

  validateToNextPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && this.state.confirmDirty) {
      form.validateFields(['confirm'], { force: true });
    }
    callback();
  };

  render() {
    const { history } = this.props;
    const { getFieldDecorator } = this.props.form;
    return (
      <SignUpStyleWrapper className="isoSignUpPage">
        <div className="isoSignUpContentWrapper">
          <div className="isoSignUpContent">
            <div className="isoLogoWrapper">
              <Link to="/dashboard">
                <IntlMessages id="page.signUpTitle" />
              </Link>
            </div>

            <Form className="isoSignUpForm" onSubmit={this.handleSignUp}>
              <div className="isoInputWrapper isoLeftRightComponent" >
                <FormItem hasFeedback>
                  {getFieldDecorator('inputFirstName', {
                    rules: [
                      {
                        required: true,
                        message: 'Please input your First Name!',
                      },
                    ],
                  })(<Input id="inputFirstName" name="inputFirstName" size="large" placeholder="First name" style={{marginRight : 0}}/>)}
                </FormItem>
                <FormItem hasFeedback>
                  {getFieldDecorator('inputLastName', {
                    rules: [
                      {
                        required: true,
                        message: 'Please input your Last Name!',
                      },
                    ],
                  })(<Input id="inputLastName" name="inputLastName" size="large" placeholder="Last name" style={{marginRight : 0}}/>)}
                </FormItem>
              </div>

              <div className="isoInputWrapper">
                <FormItem hasFeedback>
                  {getFieldDecorator('inputUserName', {
                    rules: [
                      {
                        required: true,
                        message: 'Please input your User Name!',
                      },
                    ],
                  })(<Input id="inputUserName" name="inputUserName" size="large" placeholder="User name" />)}
                </FormItem>
              </div>

              <div className="isoInputWrapper">
                <FormItem hasFeedback>
                  {getFieldDecorator('email', {
                    rules: [
                      {
                        type: 'email',
                        message: 'The input is not valid E-mail!',
                      },
                      {
                        required: true,
                        message: 'Please input your User Email!',
                      },
                    ],
                  })(<Input id="email" name="email" size="large" placeholder="Email" />)}
                </FormItem>
              </div>

              <div className="isoInputWrapper">
                <FormItem hasFeedback>
                  {getFieldDecorator('password', {
                    rules: [
                      {
                        required: true,
                        message: 'Please input your password!',
                      },
                      {
                        validator: this.validateToNextPassword,
                      },
                    ],
                  })(<Input.Password id="password" name="password" size="large" placeholder="Password" />)}
                </FormItem>
              </div>

              <div className="isoInputWrapper">
                <FormItem hasFeedback>
                  {getFieldDecorator('confirm', {
                    rules: [
                      {
                        required: true,
                        message: 'Please confirm your password!',
                      },
                      {
                        validator: this.compareToFirstPassword,
                      },
                    ],
                  })(<Input.Password id="confirm" name="confirm" size="large" placeholder="Confirm Password" onBlur={this.handleConfirmBlur}/>)}
                </FormItem>
              </div>

              <div className="isoInputWrapper" style={{ marginBottom: '50px' }}>
                <Form.Item>
                  {getFieldDecorator('terms', {
                    valuePropName: 'checked',
                    initialValue: false,
                    rules: [{
                      required: true,
                      transform: value => (value || undefined),  // Those two lines
                      type: 'boolean',                           // Do the magic
                      message: 'Please agree the terms and conditions.',
                    }],
                  })(
                    <Checkbox id="terms" name="terms">
                      I have read the <a href="">agreement</a>
                    </Checkbox>,
                  )}
                </Form.Item>
              </div>

              <div className="isoInputWrapper">
                <Button type="primary" htmlType="submit">
                  <IntlMessages id="page.signUpButton" />
                </Button>
              </div>
              <div className="isoInputWrapper isoCenterComponent isoHelperWrapper">
                <Link to="/signin">
                  <IntlMessages id="page.signUpAlreadyAccount" />
                </Link>
              </div>
            </Form>
          </div>
        </div>
      </SignUpStyleWrapper>
    );
  }
}



const WrappedFormSignUp = Form.create()(SignUp);


export default connect(
  state => ({
    isLoggedIn: state.Auth.idToken !== null ? true : false,
  }),
  { login, signup, clearMenu }
)(WrappedFormSignUp);
